        <template #default="{ node, data }">
          <span class="custom-tree-node">
            <span>{{ node.label }}</span>
            <!-- v-if="display==true" -->
            <span class="dispic">
              <img style="margin-right:15px" src="../../image/edit.png" @click="append(data)" />
              <img src="../../image/del.png" />
            </span>
          </span>
        </template>
      </el-tree>
    </div>
    <div>
        <departinfo ref="departinfo"></departinfo>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from "@vue/reactivity";
import { getCurrentInstance } from "@vue/runtime-core";
import departinfo from "../departinfo/departinfo.vue";
export default {
  components: {
    departinfo,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      data: [
        {
          dispaly: false,
          id: "1",
          label: "乐成企业",
          children: [
            {
              id: "4",
              label: "销售部",
              children: [
                {
                  id: "9",
                  label: "质控品",
                },
                {
                  id: "10",
                  label: "第三方物流",
                },
              ],
            },
            {
              id: "5",
              label: "人事部",
            },
            {
              id: "6",
              label: "财务部",
            },
          ],
        },
      ],
    });
    const reset = () => {
        proxy.$refs.departinfo.departshow();
    };
     const append = () => {
        proxy.$refs.departinfo.departshow();
    };
    return {
      ...toRefs(state),
      reset,
      append
    };
  },
};
</script>

